<script>
import * as echarts from "echarts";
import {ref, onMounted, onUnmounted} from "vue";
import chinaJson from '../assets/china.json'
import 'echarts-gl';
import 'echarts/map/js/world.js';
import geoJson from 'echarts/map/json/world.json';
import 'echarts-extension-amap';
// import jsonp from 'jsonp'
export default {
  name: 'ThermalMap',
  props: {
    width: String,
    height: String,
    data: {
      type: Array,
      required: true,
      value:""
    }
  },
  setup() {
    let myEcharts = echarts;
    const chartRef = ref(null);
    let chart = null;
    const data = [
      {
        name: 'China',
        value: 90,
      },
      {
        name: 'United States',
        value: 80,
      },
      {
        name: 'Japan',
        value: 70,
      },
    ];
    onMounted(() => {
      // initChart();
      initChart2();
      initChart3();
    });
    onUnmounted(() => {
      myEcharts.dispose;
      chart.dispose();
      chart = null;
    });

    function initChart3() {
      let chart = myEcharts.init(document.getElementById("charts"), "purple-passion");
      // 
      myEcharts.registerMap('world', geoJson);
      // echartsoption
      chart.setOption({
        visualMap: {
          min: 0,
          max: 100,
          inRange: {
            color: ['#FFFF00', '#FF0000'],
          },
          textStyle: {
            color: '#fff',
          },
        },
        geo: {
          map: 'world',
          roam: true,
          itemStyle: {
            areaColor: '#323c48',
            borderColor: '#111',
          },
          label: {
            show: false,
            color: '#fff',
            emphasis: {
              label: {
                show: true // 
              }
            }
          },
        },
        series: [
          {
            type: 'heatmap',
            coordinateSystem: 'geo',
            data: data.map(({name, value}) => ({
              name,
              value: [Math.random() * 360 - 180, Math.random() * 180 - 90, value],
            })),
            pointSize: 5,
            blurSize: 6,
          },
        ],
      });
      window.onresize = function () {
        chart.resize();
      };
    }

    function initChart2() {
      let chart = myEcharts.init(document.getElementById("myChart"), "purple-passion");
      // 
      myEcharts.registerMap('china', chinaJson);
      // 
      chart.setOption({
        title: {
          text: "",
          left: "center",
        },
        series: [
          {
            type: 'map',
            map: 'china',
            // 
            label: {
              show: true,
              fontSize: 10,
              color: 'white',
            },
            // 
            itemStyle: {
              areaColor: 'blue',
              borderColor: 'white',
              borderWidth: '1',
            },
            zoom: 1.2,
            // 
            data: [
              {name: '', value: 2005},
              {name: '', value: 1547},
              {name: '', value: 3168},
              {name: '', value: 6992},
              {name: '', value: 4200},
              {name: '', value: 9500},
            ],
          }
        ],
        visualMap: {
          min: 1000,
          max: 10000,
          text: ['High', 'Low'],
          realtime: false,
          calculable: true,
          inRange: {
            color: ['lightskyblue', 'yellow', 'orangered']
          }
        },
      });
      window.onresize = function () {
        chart.resize();
      };
    }

    /*   function initChart() {
         let chart = myEcharts.init(document.getElementById("myEcharts"), "purple-passion");
         chart.setOption({
